/* Normalise CSS */
@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Poppins:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  overflow: hidden;
  background-color: aliceblue;
}

h1 {
  text-align: center;
  font-family: "Abril Fatface", cursive;
  font-size: 6vw;
  margin: 20px;
}

/* Form CSS */
form {
  border: 2px solid black;
  background-color: #ffffff;
  min-width: 500px;
  width: 60vw;
  padding: 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 8px;
  font-size: 3vw;
  font-family: "Poppins", sans-serif;
}

form h2 {
  margin-bottom: 20px;
}

form div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

form input {
  padding: 5px;
  font-size: 2vw;
  margin-left: 5px;
}

/* Game CSS */
.main-container {
  width: 25vw;
  min-width: 300px;
  margin: 30px auto;
}

.score-container {
  width: 25vw;
  min-width: 300px;
  height: 5vw;
  min-height: 60px;
  margin-bottom: 10px;
  border: 2px solid black;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  padding-inline: 10px;
  background-color: #ffffff;
}

.score-container .player-1 {
  overflow-y: auto;
  height: 100%;
  width: 10vw;
  min-width: 120px;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
  cursor: pointer;
  transition: border-bottom-color ease-in-out 0.5s;
}

.score-container .player-2 {
  overflow-y: auto;
  height: 100%;
  width: 10vw;
  min-width: 120px;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: blue;
  cursor: pointer;
  transition: border-bottom-color ease-in-out 0.5s;
}

.game-container {
  width: 25vw;
  min-width: 300px;
  height: 25vw;
  min-height: 300px;
  border: 2px solid black;
  border-radius: 5px;
  padding: 15px;
  background-color: #ffffff;
}

.game-container .row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 13px;
}

.game-container .col {
  width: 7vw;
  min-width: 80px;
  height: 7vw;
  min-height: 80px;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
  /* text-align: center;
  padding-top: 8px; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  font-family: "Poppins", sans-serif;
}

.main-container .reset-btn-container {
  width: 25vw;
  min-width: 300px;
  text-align: center;
  margin: 0 auto;
}

/* Same CSS for Form & Game Button*/
#join-btn,
.reset-btn {
  width: 150px;
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(82, 82, 229);
  color: #ffffff;
}
